"use client";
import { useEffect, useState } from "react";
import { useAuth } from "../../context/AuthContext";
import { useAuthGuard } from "../../hooks/useAuthGuard";
import axios from "axios";

interface Employee {
	id: number;
	name: string;
	lastName: string;
	rate: number;
	workDays: number;
}

export default function EmployeesPage() {
	const { token } = useAuth();
	const [employees, setEmployees] = useState<Employee[]>([]);
	const [modalEmployee, setModalEmployee] = useState<Employee | null>(null);
	const [isEdit, setIsEdit] = useState(false);

	const fetchEmployees = async () => {
		if (!token) return;
		try {
		const res = await axios.get("http://localhost:4000/employees", {
			headers: { Authorization: `Bearer ${token}` },
		});
		setEmployees(res.data);
		} catch (err) {
		console.log("Failed to fetch employees");
		}
	};

	useEffect(() => {
		fetchEmployees();
	}, [token]);

	const handleSubmit = async (e: React.FormEvent) => {
		e.preventDefault();
		if (!modalEmployee) return;

		try {
		if (isEdit) {
			await axios.put(
			`http://localhost:4000/employees/${modalEmployee.id}`,
			modalEmployee,
			{ headers: { Authorization: `Bearer ${token}` } }
			);
		} else {
			await axios.post(
			"http://localhost:4000/employees",
			modalEmployee,
			{ headers: { Authorization: `Bearer ${token}` } }
			);
		}
		setModalEmployee(null);
		fetchEmployees();
		const modalEl = document.getElementById("employeeModal");
		if (modalEl) (window as any).bootstrap.Modal.getInstance(modalEl)?.hide();
		} catch (err) {
		alert("Failed to save employee");
		}
	};

	const handleDelete = async (id: number) => {
		if (!confirm("Are you sure?")) return;
		try {
		await axios.delete(`http://localhost:4000/employees/${id}`, {
			headers: { Authorization: `Bearer ${token}` },
		});
		fetchEmployees();
		} catch (err) {
		alert("Failed to delete employee");
		}
	};

return (
	<div className="container py-5">
		<h2>Employees</h2>
		<button className="btn btn-success mb-3" data-bs-toggle="modal" data-bs-target="#employeeModal" onClick={() => { setModalEmployee({ id: 0, name: "", lastName: "", rate: 0, workDays: 0 }); setIsEdit(false); }}> Add Employee </button>
		<table className="table table-striped">
			<thead>
				<tr>
					<th>Name</th>
					<th>Last Name</th>
					<th>Rate</th>
					<th>Work Days</th>
					<th>Actions</th>
				</tr>
			</thead>
			<tbody>
				{employees.map((emp) => (
					<tr key={emp.id}>
						<td>{emp.name}</td>
						<td>{emp.lastName}</td>
						<td>{emp.rate}</td>
						<td>{emp.workDays}</td>
						<td>
							<button
							className="btn btn-primary me-2"
							data-bs-toggle="modal"
							data-bs-target="#employeeModal"
							onClick={() => {
								setModalEmployee(emp);
								setIsEdit(true);
							}}
							>
							Edit
							</button>
							<button className="btn btn-danger" onClick={() => handleDelete(emp.id)}>
							Delete
							</button>
						</td>
					</tr>
				))}
			</tbody>
		</table>

		{/* Modal */}
		<div className="modal fade" id="employeeModal" tabIndex={-1} aria-hidden="true">
			<div className="modal-dialog">
				<form className="modal-content" onSubmit={handleSubmit}>
					<div className="modal-header">
						<h5 className="modal-title">{isEdit ? "Edit" : "Add"} Employee</h5>
						<button type="button" className="btn-close" data-bs-dismiss="modal"></button>
					</div>
					<div className="modal-body">
						<input
							type="text"
							className="form-control mb-2"
							placeholder="Name"
							value={modalEmployee?.name || ""}
							onChange={(e) => setModalEmployee((prev) => ({ ...prev!, name: e.target.value }))}
							required
						/>
						<input
							type="text"
							className="form-control mb-2"
							placeholder="Last Name"
							value={modalEmployee?.lastName || ""}
							onChange={(e) => setModalEmployee((prev) => ({ ...prev!, lastName: e.target.value }))}
							required
						/>
						<input
							type="number"
							className="form-control mb-2"
							placeholder="Rate"
							value={modalEmployee?.rate || 0}
							onChange={(e) => setModalEmployee((prev) => ({ ...prev!, rate: Number(e.target.value) }))}
							required
						/>
						<input
							type="number"
							className="form-control mb-2"
							placeholder="Work Days"
							value={modalEmployee?.workDays || 0}
							onChange={(e) => setModalEmployee((prev) => ({ ...prev!, workDays: Number(e.target.value) }))}
							required
						/>
					</div>
					<div className="modal-footer">
						<button type="button" className="btn btn-secondary" data-bs-dismiss="modal">
							Close
						</button>
						<button type="submit" className="btn btn-primary">
							{isEdit ? "Save Changes" : "Add Employee"}
						</button>
					</div>
				</form>
			</div>
		</div>
	</div>
);
}
